import {Logger,Message,Browser,Utils} from './utils';
class Client {
    //construct a meeting client with signal client and rtc client
    constructor(sclient, rclient, localAccount) {
        this.signal = sclient;
        this.rtc = rclient;
        this.localAccount = localAccount;
        this.channelName='';
        //ring tones resources
        this.sound_ring = new Howl({
            // src: ['../../../../../static/sound/basic_ring.mp3'],
            src: ['https://rexyanglucky.github.io/video-call/assets/sound/basic_ring.mp3'],
            
            loop: true
        });

        this.sound_tones = new Howl({
            // src: ['../../../../../sound/basic_tones.mp3'],
            src: ['https://rexyanglucky.github.io/video-call/assets/sound/basic_tones.mp3'],
            
            loop: true
        });

        this.signal.onInviteReceived = $.proxy(this.onInviteReceived, this);
        this.signal.onInviteEndByPeer = $.proxy(this.onInviteEndByPeer, this);
        this.signal.onMessageChannelReceive = $.proxy(this.onMessageChannelReceive, this);

        this.subscribeEvents();
    }

    sendMessage(text) {
        if (!text.trim()) return false // empty
        let chatMsgContainer = $(".chart_warp");
        let msg_item = { ts: new Date(), text: text, account: this.localAccount };
        chatMsgContainer.append(this.buildMsg(text, true, msg_item.ts));
        chatMsgContainer.scrollTop(chatMsgContainer[0].scrollHeight)
        this.signal.broadcastMessage(JSON.stringify(msg_item));
    }
    buildMsg(msg, me, ts) {
        let html = "";
        let timeStr =ts.toString();
        if (timeStr) {
            html += `<div>${timeStr}</div>`
        }
        let className = me ? "message right clearfix" : "message clearfix";
        html += "<li class=\"" + className + "\">";
        html += "<img src=\"https://s3-us-west-2.amazonaws.com/s.cdpn.io/245657/1_copy.jpg\">";
        html += "<div class=\"bubble\">" + Utils.safe_tags_replace(msg) + "<div class=\"corner\"></div>";
        html += "<span>" + this.parseTwitterDate(ts) + "</span></div></li>";

        return html;
    }
    parseTwitterDate(tdate) {
        var system_date = new Date(Date.parse(tdate));
        var user_date = new Date();
        // if (K.ie) {
        //     system_date = Date.parse(tdate.replace(/( \+)/, ' UTC$1'))
        // }
        var diff = Math.floor((user_date - system_date) / 1000);
        if (diff <= 1) { return "just now"; }
        if (diff < 20) { return diff + " seconds ago"; }
        if (diff < 40) { return "half a minute ago"; }
        if (diff < 60) { return "less than a minute ago"; }
        if (diff <= 90) { return "one minute ago"; }
        if (diff <= 3540) { return Math.round(diff / 60) + " minutes ago"; }
        if (diff <= 5400) { return "1 hour ago"; }
        if (diff <= 86400) { return Math.round(diff / 3600) + " hours ago"; }
        if (diff <= 129600) { return "1 day ago"; }
        if (diff < 604800) { return Math.round(diff / 86400) + " days ago"; }
        if (diff <= 777600) { return "1 week ago"; }
        return "on " + system_date;
    }
    onMessageChannelReceive(account, msg) {
        this.onReceiveMessage(account, msg, "channel");
    }
    onReceiveMessage(account, msg, type) {
        let client = this;
        let chatMsgContainer = $(".chart_warp");
        let msgJson=JSON.parse(msg);
        chatMsgContainer.append(client.buildMsg(msgJson.text, false, new Date()));
        chatMsgContainer.scrollTop(chatMsgContainer[0].scrollHeight)
       
    }

    //return a promise resolves a remote account name
    requestRemoteAccount() {
        let deferred = $.Deferred();
        let localAccount = this.localAccount;

        let account = Browser.getParameterByName("remoteaccount");
        deferred.resolve(account);

        return deferred;
    }

    //return a promise resolves a signaling call result
    call(channelName, account, requirePeerOnline) {
        let deferred = $.Deferred();
        let dialog = $(".callingModal");
        dialog.find(".callee").html(account);
        let signal = this.signal;

        signal.call(channelName, account, requirePeerOnline).done(_ => {
            dialog.hide();
            $('.op_warp').hide();
            deferred.resolve();
        }).catch($.proxy(err => {
            Message.show(err.reason);
            deferred.reject();
        }, this));

        return deferred.promise();
    }

    //end given call object, passive means the call is ended by peer
    endCall(call, passive) {
        let deferred = $.Deferred();
        let signal = this.signal;
        let rtc = this.rtc;
        let btn = $(".toolbar .muteBtn");

        $('.op_warp').show();
        rtc.muted = true;
        btn.removeClass("btn-info").addClass("btn-secondary");
        btn.find("i").html("mic");

        //end rtc
        rtc.end();
        //end signal call
        signal.endCall(call, passive);
        return deferred.promise();
    }

    //ring when calling someone else
    ringCalling(play) {
        if (play) {
            this.sound_ring.play();
        } else {
            this.sound_ring.stop();
        }
    }
    //ring when being called by someone else
    ringCalled(play) {
        if (play) {
            this.sound_tones.play();
        } else {
            this.sound_tones.stop();
        }
    }

    //events
    subscribeEvents() {
        let signal = this.signal;
        let client=this;
        let rtc = this.rtc;
        //toolbar end call btn
        $(".toolbar .endCallBtn").off("click").on("click", $.proxy(e => {
            this.ringCalling(false);
            this.endCall(signal.call_active || signal.call_holding, false);
        }, this));

        //toolbar mute btn
        $(".toolbar .muteBtn").off("click").on("click", $.proxy(e => {
            let btn = $(e.currentTarget);
            rtc.toggleMute();
            if (rtc.muted) {
                btn.removeClass("btn-secondary").addClass("btn-info");
                btn.find("i").html("mic_off");
            } else {
                btn.removeClass("btn-info").addClass("btn-secondary");
                btn.find("i").html("mic");
            }
        }, this));



        $('#btn_send_call').off('click').on('click', $.proxy(e => {
            this.requestRemoteAccount().done($.proxy(remoteAccount => {
                //start calling via signal
                if (remoteAccount !== "") {
                    this.ringCalling(true);
                    $.when(rtc.init(client.channelName, false), this.call(client.channelName, remoteAccount, true)).done($.proxy((stream, _) => {
                        this.ringCalling(false);
                        this.rtc.rtc.publish(stream);
                    }, this)).catch($.proxy(_ => {
                        this.ringCalling(false);
                        this.endCall(signal.call_active || signal.call_holding, false);
                    }, this));
                }
            }, this));
        }, this));


        $('#message_to_send').off("keydown").on("keydown", function (e) {
            if (e.keyCode == 13) {
                e.preventDefault();
                client.sendMessage($(this).val());
                $(this).val("");
            }
        });
        $('#btn_send_msg').off('click').on('click',function(){
            client.sendMessage($('#message_to_send').val());
            $('#message_to_send').val("");
        })
    }

    //delegate callback when receiving call
    onInviteReceived(call) {
        let dialog = $(".calledModal");
        let signal = this.signal;
        let rtc = this.rtc;

        dialog.find(".caller").html(call.peer);
        dialog.find(".declineBtn").off("click").on("click", (e) => {
            // dialog.modal('hide');
            dialog.hide();
            this.ringCalled(false);
            signal.rejectCall(call, 0);
        });

        dialog.find(".acceptBtn").off("click").on("click", (e) => {
            dialog.hide();
            $(".startCallBtn").hide();
            this.ringCalled(false);
            signal.acceptCall(call).done(call => {
                rtc.init(call.channelName, true);
            }).catch(err => {
                Logger.log(`Accept call failed: ${err}`);
            });
        });

        this.ringCalled(true);
        dialog.show();
    }

    //delegate callback called when call end by peer
    onInviteEndByPeer() {
        let signal = this.signal;
        // $(".calledModal").modal('hide');
        $(".calledModal").hide();
        this.ringCalled(false);
        this.endCall(signal.call_active || signal.call_holding, true);
    }
}
export default Client;



